import React, { useState } from 'react'
import 'react-datepicker/dist/react-datepicker.css'
import { useForm } from 'react-hook-form'
import { useDispatch, useSelector } from 'react-redux'
import { selectBuild } from '../../../store/build'
import { selectCompanyDetails } from '../../../store/companies'
import { dealerAdminFields, selectDealerAdmin, updateDealerAdmin } from '../../../store/dealerAdmin'
import { formatCurrency } from '../../../utils/utils'
import { FieldSet, Form, Label, Legend, TextArea, TopLabelText } from '../../common/forms'
import { Heading3, Strong } from '../../common/style'
import DiscountModal from '../DiscountModal/DiscountModal'

export default function ScreenDealerAdminPage2() {
  const dispatch = useDispatch()
  const dealerAdmin = useSelector(selectDealerAdmin)
  const build = useSelector(selectBuild)
  const [showDiscountModal, setShowDiscountModal] = useState(false)
  const companies = useSelector(selectCompanyDetails)

  const { register, errors, getValues, triggerValidation, control } = useForm<dealerAdminFields>({
    defaultValues: dealerAdmin,
    mode: 'onChange',
  })

  const handleChange = async () => {
    dispatch(updateDealerAdmin(getValues()))
    const result = await triggerValidation()
  }

  return (
    <Form onChange={handleChange} className="szForm">
      <FieldSet>
        <Legend>Special Requests</Legend>
        <Label htmlFor="specialRequests">
          <TopLabelText>ECR Requests</TopLabelText>
          <TextArea
            id="specialRequests"
            name="specialRequests"
            placeholder="Engineering Change Request here..."
            ref={register({ required: false })}
          />
        </Label>

        <Label htmlFor="comments">
          <TopLabelText>Comments</TopLabelText>
          <TextArea id="comments" name="comments" placeholder="Other comments..." ref={register({ required: false })} />
        </Label>

        <Heading3
          onClick={() => {
            setShowDiscountModal((show) => !show)
            console.log('123')
          }}>
          Total Price{' '}
          <Strong strikethrough={parseInt(dealerAdmin.discount) > 0}>{formatCurrency(build.totalPrice)}</Strong>
        </Heading3>
      </FieldSet>
      <DiscountModal show={showDiscountModal} />
    </Form>
  )
}
