import styled from 'styled-components/macro'

interface labelProps {
  transparent?: boolean
}

export const Form = styled.form`
  padding-top: 32px;
  margin-bottom: 32px;
  box-sizing: border-box;
`

export const Input = styled.input`
  border: 0;
  padding: 14px 10px;
  flex-grow: 1;

  &:active,
  &:focus {
    outline: none;
  }
`
export const Radio = styled.input.attrs({ type: 'radio' })`
  border: 0;
  padding: 14px 10px;
  flex-grow: 0;
  display: block;
  &:active,
  &:focus {
    outline: none;
  }
`

export const TextArea = styled.textarea`
  width: 100%;
  border: 0;
  padding: 14px 10px;
  &:focus {
    outline: none;
  }
  min-height: 70px;
`

export const Checkbox = styled.input.attrs({ type: 'checkbox' })`
  width: 20px;
  flex-grow: 0;
`

export const LabelText = styled.span`
  width: 120px;
  display: inline-block;
`
export const TopLabelText = styled(LabelText)`
  padding-top: 10px;
  align-self: flex-start;
`
export const Label = styled.label<labelProps>`
  margin-bottom: 16px;
  background-color: ${(p) => (p.transparent ? 'transparent' : 'white')};
  box-shadow: ${(p) => (p.transparent ? 'none' : '0px 2px 10px rgba(0, 0, 0, 0.02);')};
  padding: ${(p) => (p.transparent ? '0' : '0 12px;')};
  display: flex;
  align-items: center;

  &:focus-within {
    box-shadow: ${(p) => (p.transparent ? 'none' : '0px 2px 10px rgba(0, 0, 0, 0.02), 0 0 3px var(--grey-3)')};
  }
`

export const DateLabel = styled(Label)`
  padding: 14px 10px;
`
export const FieldSet = styled.fieldset`
  border: 0;
  margin: 0;
  padding: 0;
`

export const Legend = styled.legend`
  font-weight: 700;
  margin-bottom: 0.8rem;
`
export const GridSet = styled.fieldset`
  display: grid;
  grid-auto-rows: min-content;
  margin: 0 0 14px;
  gap: 10px;
  grid-template-columns: 1fr 1fr;
  border: 1px solid var(--grey-2);

  & ${Label} {
    margin-bottom: 0;
  }

  & ${Legend} {
    margin-bottom: 0;
  }
`

export const RadioSet = styled.fieldset`
  display: flex;
  align-items: flex-start;
  gap: 14px;
  margin-bottom: 14px;
  border: 1px solid var(--grey-2);

  & ${Legend} {
    margin-bottom: 0;
  }
  & ${Label} {
    margin-bottom: 0;
  }
`
export const Select = styled.select`
  padding: 14px 10px;
  border: 0;
  width: 100%;
`

export const ErrorMessage = styled.span``
