import React, { useRef } from 'react'
import { VscAdd, VscCheck, VscZoomIn, VscZoomOut } from 'react-icons/vsc'
import ReactMarkdown from 'react-markdown'
import { useDispatch, useSelector } from 'react-redux'
import { A11y, Navigation, Pagination, Zoom } from 'swiper'
import 'swiper/css'
import 'swiper/css/lazy'
import 'swiper/css/navigation'
import 'swiper/css/pagination'
import 'swiper/css/zoom'
import { Swiper, SwiperSlide } from 'swiper/react'
import { addAccessory, removeAccessory, selectBuildAccessoryIds } from '../../../store/build'
import { selectAcc2ById } from '../../../store/catalog'
import { selectDisplayedId } from '../../../store/ui'
import { formatCurrency } from '../../../utils/utils'
import { Button, Heading2, Spacer } from '../../common/style'
import Placeholder from '../Placeholder/Placeholder'
import { ZoomButton } from '../ScreenAccessoriesPicker/style'
import { ImageWrapper, Price, ProductImage, ProductInfo, Screen, SlidesFooter } from './../ProductDetails/style'

export default function AccessoryDetails() {
  const id = useSelector(selectDisplayedId)
  const accessory = useSelector(selectAcc2ById(id ?? ''))
  const slides = accessory?.slideshow_images
  const buildAccessories = useSelector(selectBuildAccessoryIds)
  const dispatch = useDispatch()
  const isAdded = buildAccessories.find((accId) => accId === accessory?.id ?? '') !== undefined
  const ref = useRef<HTMLDivElement>(null)

  const img = accessory?.featured_image?.url ? (
    <ProductImage src={'' + process.env.REACT_APP_ASSETS_URL + accessory?.featured_image?.url} alt="" />
  ) : (
    <Placeholder />
  )

  return (
    <Screen>
      <ImageWrapper>
        {slides ? (
          <>
            <Swiper
              /* @ts-ignore */
              ref={ref}
              modules={[Navigation, Pagination, A11y, Zoom]}
              preloadImages={false}
              navigation
              pagination={{ clickable: true }}
              spaceBetween={0}
              slidesPerView={1}
              zoom
              lazy
              onSlideChange={() => console.log('slide change')}
              onSwiper={(swiper) => console.log(swiper)}>
              {slides?.map((slide, idx) => (
                <SwiperSlide zoom key={idx}>
                  <img src={slide.url} alt="" />
                </SwiperSlide>
              ))}
            </Swiper>
            <SlidesFooter>
              <ZoomButton
                onClick={() => {
                  /* @ts-ignore */
                  ref.current.swiper.zoom.out()
                }}>
                <VscZoomOut />
              </ZoomButton>
              <ZoomButton
                onClick={() => {
                  /* @ts-ignore */
                  ref.current.swiper.zoom.in()
                }}>
                <VscZoomIn />
              </ZoomButton>
            </SlidesFooter>
          </>
        ) : (
          img
        )}
      </ImageWrapper>
      <ProductInfo>
        <Heading2>{accessory && accessory?.name}</Heading2>
        {accessory?.short_description && <div>{accessory.short_description}</div>}
        {accessory?.rrp && <Price>{accessory?.rrp && formatCurrency(accessory.rrp)}</Price>}
        {accessory?.description && <ReactMarkdown>{accessory?.description + ''}</ReactMarkdown>}
        <Spacer gap={20}></Spacer>
        <Button
          toggled={isAdded}
          onClick={() => {
            console.log('is?', isAdded)
            isAdded ? dispatch(removeAccessory(id + '')) : dispatch(addAccessory(id + ''))
          }}>
          {isAdded ? (
            <>
              <VscCheck /> Added
            </>
          ) : (
            <>
              <VscAdd /> Add to Build
            </>
          )}
        </Button>
      </ProductInfo>
    </Screen>

    // <DetailsWrapper>
    //   <Col>
    //     {accessory && (
    //       <img
    //         src={accessory.featured_image && process.env.REACT_APP_ASSETS_URL + accessory.featured_image.url}
    //         alt=""
    //       />
    //     )}
    //   </Col>
    //   <Col>
    //     <Heading3>{accessory?.name}</Heading3>
    //     {accessory?.rrp && <Heading4>${accessory?.rrp}</Heading4>}
    //     {accessory?.description}
    //   </Col>
    // </DetailsWrapper>
  )
}
