import React, { useState } from 'react'
import DatePicker from 'react-datepicker'
import 'react-datepicker/dist/react-datepicker.css'
import { Controller, useForm } from 'react-hook-form'
import { useDispatch, useSelector } from 'react-redux'
import { selectBuild } from '../../../store/build'
import { selectCompanyDetails } from '../../../store/companies'
import { dealerAdminFields, selectDealerAdmin, updateDealerAdmin } from '../../../store/dealerAdmin'
import {
  Checkbox,
  DateLabel,
  FieldSet,
  Form,
  GridSet,
  Input,
  Label,
  LabelText,
  Legend,
  Select,
} from '../../common/forms'

export default function ScreenDealerAdmin() {
  const dispatch = useDispatch()
  const dealerAdmin = useSelector(selectDealerAdmin)
  const build = useSelector(selectBuild)
  const [showDiscountModal, setShowDiscountModal] = useState(false)
  const companies = useSelector(selectCompanyDetails)

  const { register, errors, getValues, triggerValidation, control } = useForm<dealerAdminFields>({
    defaultValues: dealerAdmin,
    mode: 'onChange',
  })

  const handleChange = async () => {
    dispatch(updateDealerAdmin(getValues()))
    const result = await triggerValidation()
  }

  return (
    <Form onChange={handleChange} className="szForm">
      <FieldSet>
        {/* <Legend>Enter your contact details</Legend> */}
        <FieldSet>
          <Legend>Dealer Details</Legend>

          <Label htmlFor="dealerId">
            <LabelText>Dealer</LabelText>
            <Select name="dealerId" id="dealerId" defaultValue={companies[0].id} ref={register}>
              {companies.map((company, idx) => (
                <option value={company?.id} key={idx}>
                  {company?.name}
                </option>
              ))}
            </Select>
          </Label>
        </FieldSet>

        <Label htmlFor="salesperson">
          <LabelText>Sales Person</LabelText>
          <Input id="salesperson" name="salesperson" placeholder="John Smith" ref={register({ required: true })} />
        </Label>

        <Label htmlFor="location">
          <LabelText>Location</LabelText>
          <Select name="location" id="location" defaultValue="tradeshow" ref={register}>
            <option value="tradeshow">Tradeshow</option>
            <option value="showroom">Showroom</option>
          </Select>
        </Label>

        <Label transparent htmlFor="matchWheels">
          <Checkbox id="matchWheels" type="checkbox" name="matchWheels" defaultChecked={false} ref={register} />
          Match Customer wheels
        </Label>
        {getValues().matchWheels && (
          <GridSet>
            <Legend>Tow Vehicle Details</Legend>
            <Label htmlFor="towVehicleMake">
              <LabelText>Vehicle Make</LabelText>
              <Input
                id="towVehicleMake"
                name="towVehicleMake"
                placeholder="Toyota"
                ref={register({ required: getValues().matchWheels })}
              />
            </Label>
            <Label htmlFor="towVehicleModel">
              <LabelText>Model</LabelText>
              <Input
                id="towVehicleModel"
                name="towVehicleModel"
                placeholder=""
                ref={register({ required: getValues().matchWheels })}
              />
            </Label>
            <Label htmlFor="towVehicleYear">
              <LabelText>Year</LabelText>
              <Input
                id="towVehicleYear"
                name="towVehicleYear"
                placeholder="2022"
                ref={register({ required: getValues().matchWheels })}
              />
            </Label>
            <Label htmlFor="towVehicleWheelSize">
              <LabelText>Wheel Size</LabelText>
              <Input
                id="towVehicleWheelSize"
                name="towVehicleWheelSize"
                placeholder=""
                ref={register({ required: getValues().matchWheels })}
              />
            </Label>
            <Label htmlFor="towVehicleTyreSize">
              <LabelText>Tyre Size</LabelText>
              <Input
                id="towVehicleTyreSize"
                name="towVehicleTyreSize"
                placeholder=""
                ref={register({ required: getValues().matchWheels })}
              />
            </Label>
          </GridSet>
        )}

        <DateLabel htmlFor="buildDate">
          <LabelText>Build Date</LabelText>
          <Controller
            id="buildDate"
            as={DatePicker}
            name="buildDate"
            placeholderText="Select Date"
            control={control}
            valueName="selected"
          />
        </DateLabel>
      </FieldSet>
    </Form>
  )
}
