import React, { useState } from 'react'
import { ImSpinner8 } from 'react-icons/im'
import { VscCreditCard, VscMail } from 'react-icons/vsc'
import { useDispatch, useSelector } from 'react-redux'
import {
  callAddToCart,
  selectBuild,
  selectBuildAccessories,
  selectBuildCost,
  selectBuildImage,
  selectBuildModel,
  selectBuildProduct,
} from '../../../store/build'
import { selectCompanyById } from '../../../store/companies'
import { selectCustomerDetails } from '../../../store/customer'
import { formatCurrency } from '../../../utils/utils'
import { Button, Heading1, Heading3, Heading4 } from '../../common/style'
import { LOCALIZED_ATTRS } from './../../../globals'
import { AccentText, ButtonGroup, Price, ProductImage, ProductImageWrapper, Wrapper } from './style'

export default function ThankYouScreen() {
  const product = useSelector(selectBuildProduct)
  const model = useSelector(selectBuildModel)
  const accessories = useSelector(selectBuildAccessories)
  const image = useSelector(selectBuildImage)
  const dispatch = useDispatch()
  const [emailSent, setEmailSent] = useState(false)
  const fullBuild = useSelector(selectBuild)
  const customer = useSelector(selectCustomerDetails)
  const [loadingAddToCart, setLoading] = useState(false)
  const customerDealer = useSelector(selectCompanyById(customer.dealerId ?? ''))
  const allDetails = {
    ...fullBuild,
    customer: customer,
    dealer: customerDealer,
  }
  const sendMail = () => {
    setEmailSent(true)
    if (window.szReactPlugin && emailSent === false) {
      fetch(LOCALIZED_ATTRS?.email_endpoint, {
        method: 'POST',
        headers: {
          Accept: 'application/json, text/plain, */*',
          'Content-Type': 'application/json',
        },
        body: JSON.stringify(allDetails),
      })
        .then((response) => response.json())
        .then((data) => {
          if (data) {
            console.log(data)
          }
        })
        .catch((error) => {
          console.error(error)
        })
    }
  }

  const addToCart = () => {
    setLoading(true)
    console.log(loadingAddToCart)

    if (!LOCALIZED_ATTRS.add_to_cart_endpoint) return

    dispatch(
      callAddToCart(LOCALIZED_ATTRS.add_to_cart_endpoint, {
        ...allDetails,
        dealer: customerDealer,
      }),
    )
  }

  return (
    <Wrapper>
      <Heading3>Thank you for your enquiry</Heading3>
      <Heading1>
        <AccentText>{product?.name}</AccentText> {model?.name}
      </Heading1>
      {accessories.length > 0 && (
        <Heading4 className="thankyou-accessories">With {accessories.length} Accessories</Heading4>
      )}
      <ProductImageWrapper>
        <ProductImage src={'' + process.env.REACT_APP_ASSETS_URL + image?.url} alt="" />
      </ProductImageWrapper>
      <Price>Total {formatCurrency(useSelector(selectBuildCost))}</Price>
      <ButtonGroup>
        <Button secondary disabled={emailSent} onClick={sendMail}>
          <VscMail /> {emailSent ? 'Sent' : 'Email Me a Copy'}
        </Button>
        <Button disabled={loadingAddToCart} onClick={() => addToCart()}>
          {loadingAddToCart ? <ImSpinner8 className="spinning" /> : <VscCreditCard />}
          <span>Checkout</span>
        </Button>
      </ButtonGroup>
    </Wrapper>
  )
}
