import { createSlice, PayloadAction } from '@reduxjs/toolkit'
import { RootState } from './store'

export interface dealerAdminFields {
  companyId?: string
  salesperson?: string
  location?: string
  matchWheels: boolean
  towVehicleMake?: string
  towVehicleModel?: string
  towVehicleYear?: string
  towVehicleWheelSize?: string
  towVehicleTyreSize?: string
  specialRequests?: string
  comments?: string
  buildDateRequested?: string
  discount: string
  discountType: string
}

const initialState: dealerAdminFields = {
  matchWheels: false,
  discount: '0',
  discountType: '$',
}

export const dealerAdminSlice = createSlice({
  name: 'dealerAdmin',
  initialState,
  reducers: {
    updateDealerAdmin: (state, action: PayloadAction<dealerAdminFields>) => {
      return { ...state, ...action.payload }
    },
  },
})

export const { updateDealerAdmin } = dealerAdminSlice.actions

export const selectDealerAdmin = (state: RootState) => {
  return state.dealerAdmin
}

export default dealerAdminSlice.reducer
