import React, { useState } from 'react'
import { useForm } from 'react-hook-form'
import { useDispatch, useSelector } from 'react-redux'
import { selectBuild } from '../../../store/build'
import { dealerAdminFields, selectDealerAdmin, updateDealerAdmin } from '../../../store/dealerAdmin'
import { formatCurrency } from '../../../utils/utils'
import { Input, LabelText } from '../../common/forms'
import { Button, Strong } from '../../common/style'
import { NewPrice, Wrapper } from './style'
interface props {
  show: boolean
  // onChange: React.ChangeEventHandler
}

export default function DiscountModal({ show = false }: props) {
  const dispatch = useDispatch()
  const [type, setType] = useState('$')
  const dealerAdmin = useSelector(selectDealerAdmin)
  const build = useSelector(selectBuild)
  const [selfShow, setSelfShow] = useState(true)
  // const [isShowing, setShowing] = useState(show);
  const { register, errors, getValues, triggerValidation, control } = useForm<dealerAdminFields>({
    // defaultValues: { discount: 0 },
    mode: 'onChange',
  })

  const handleChange = () => {
    dispatch(updateDealerAdmin({ ...getValues(), discountType: type }))
  }

  return (
    <Wrapper show={show}>
      <NewPrice onClick={() => setSelfShow(true)}>
        Discounted Price&nbsp;
        {dealerAdmin.discountType === '$' ? (
          <Strong>{dealerAdmin.discount && formatCurrency(build.totalPrice - parseInt(dealerAdmin.discount))}</Strong>
        ) : (
          <Strong>
            {dealerAdmin.discount &&
              formatCurrency(build.totalPrice - 0.01 * parseInt(dealerAdmin.discount) * build.totalPrice)}
          </Strong>
        )}
      </NewPrice>
      {selfShow && (
        <>
          <LabelText>Discount ({type})</LabelText>
          <Input
            id="discountAmt"
            onChange={handleChange}
            name="discount"
            placeholder="0"
            type="number"
            ref={register({})}
          />
          <Button
            type="button"
            onClick={() => {
              setType('$')
              dispatch(updateDealerAdmin({ ...getValues(), discountType: '$' }))
            }}>
            $
          </Button>
          &nbsp;
          <Button
            type="button"
            onClick={() => {
              setType('%')
              dispatch(updateDealerAdmin({ ...getValues(), discountType: '%' }))
            }}>
            %
          </Button>
          &nbsp;
          <Button
            type="button"
            onClick={() => {
              setSelfShow(false)
            }}>
            Close
          </Button>
          {errors.discount && errors.discount.message}
          {/* </Label> */}
        </>
      )}
    </Wrapper>
  )
}
